import React, { useState } from 'react';

import Photos from './pages/photos';
import Registry from './pages/registry';

import deepPurple from '@material-ui/core/colors/deepPurple';
import { makeStyles, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Header from './components/header';
import Footer from './components/footer';

const image = require('./images/website-background.jpg');
const image2 = require('./images/website-moonboy.png');
const image3 = require('./images/babers.jpg');
const styles = {
    background: `url(${image})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
};
const styles2 = {
    justifyContent: 'center',
    display: 'flex',
    zIndex: 2001,
    position: 'fixed',
    width: '100%',
    top: 20,
    backgroundColor: 'transparent',
};
const styles3 = {
    background: `url(${image3})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    height: '50%',
    width: '50%',
    display: 'flex',
    flexDirection: 'column',
};

const theme = createMuiTheme({
    palette: {
        primary: deepPurple,
        secondary: {
            main: 'rgba(206,206,206,.9)',
        },
    },
});

const useStyles = makeStyles(() => ({
    pageComponentContainer: { height: '100%', width: '100%', paddingTop: '50px' }
}));

function App() {
    const classes = useStyles();

    const [currentPage, setCurrentPage] = useState('photos');

    let PageComponent = Photos;
    switch( currentPage ) {
        case 'photos':
            PageComponent = Photos;
            break;
        case 'registry': 
            PageComponent = Registry;
    }

    const changePage = (page) => () => setCurrentPage(page);

    return (
        <ThemeProvider theme={theme}>
            <div style={styles} >
                <Header onChangePage={changePage} currentPage={currentPage}/>
                <div style={styles2}>
                    <img src={image2} style={{ maxWidth: '75px', height: 'auto', width: '7%' }}/>
                </div>
                <div className={classes.pageComponentContainer}>
                    <PageComponent />
                </div>
                <Footer /> 
            </div>
        </ThemeProvider>
    );
}
    
export default App;
        